import uniIcon from "@/components/uni-icon/uni-icon.vue"
import { post } from '@/api/user.js'
import { api } from '@/config/common.js'
export default {
	data() {
		return {
			loading:false,
			stop: false,
			miao: 60,
			loginname:'',
			mobile:'',
			code:'',
			status:0,
			password:'',
			new_password:'',
			re_new_password:'',
			title:'登录'
		};
	},
	components: {uniIcon}, 
	onShow() {
		console.log("2213123123123");
	},
	methods: {
		handleNavbarClickLeft() {
			uni.switchTab({
				url:'/pages/my/my'
			}) 
		},
		reg_action(){
			uni.reLaunch({
				url:'/pages/my/login/reg'
			})
		},
		forget_action(){
			uni.reLaunch({
				url:'/pages/my/login/forget'
			})
		},
		handleLogin(){
			const {loginname,password} = this;
			if(loginname.length==0){
				this.$api.msg('登录账号不能为空！')
				return;
			} 
			if(password.length==0){
				this.$api.msg('登录密码不能为空！')
				return;
			} 						
			const sendData = {
				loginname,
				password
			};
			this.loading = true;
			post(api.login,sendData).then(res => {
				if (res.status == 200 && res.data.returnCode == '0000') {
					let userInfo = {
									"token":res.data.data.token,
									"exp":res.data.data.exp,
									"userId":res.data.data.userId,
								};
								uni.setStorage({
								    key: 'userInfo',
								    data: userInfo,
								    success: function () {
										//uni.$emit('tokenchage',{msg:'token变化了'});
								        uni.reLaunch({
								        	url:'/pages/index/index'
								        })
								    }
								});
				} else {
					this.$api.msg(res.data.returnMessage) 
				}
				this.loading =false;
			}).catch(error => {
				this.loading =false;
				this.$api.msg('请求失败fail') 
			})
		},
		send(){
			const mobile=this.mobile
			this.settime(60);
			if(mobile.length!=11){
				this.$api.msg('手机号错误')
				return; 
			}
		},
		settime(smiao) {
			const that = this
			const miao = that.miao
			if (miao == 0) {
				that.miao=0
				this.stop = false
			} else {
				setTimeout(() => {
					smiao--
					this.miao = smiao
					that.settime(smiao)
				}, 1000)
			}
		
		}
	}
}
